import './App.css';
import {BrowserRouter,Routes,Route} from "react-router-dom";
import SignupPage from './pages/Signup';
import LoginPage from './pages/Login';
import { ToastContainer } from 'react-toastify';
import {auth} from './firebase';
import {useAuthState} from 'react-firebase-hooks/auth';
import Profile from './pages/Profile';
import Dashboard from './pages/dashboard';
import ResetPass from './pages/Reset';
function App() {
  const [user] = useAuthState(auth);
  return (
    <main className="page-wrapper">
      <ToastContainer style={{ width: "420px" }} />
      <BrowserRouter>
          <Routes>
              <Route path="/" element={user ? <Profile/> : <LoginPage/>} />
              <Route path="/dashboard" element={<Dashboard/>} />
              <Route path="/dashboard02" element={<Dashboard/>} />
              <Route path="/signup" element={user ? <Profile/> : <SignupPage/>} />
              <Route path="/reset" element={user ? <Profile/> : <ResetPass/>} />
          </Routes>
        </BrowserRouter>
    </main>
  );
}

export default App;
