import React, { useState, useEffect } from 'react';
import { auth } from '../firebase'
import { RecaptchaVerifier ,signInWithPhoneNumber } from "firebase/auth";
import {toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from "axios";
import { Link } from 'react-router-dom';

function LoginVerify(){

    const [state, setState] = useState({
        countryCode: "",
    });

    const getGeoInfo = () => {
    axios
        .get("https://ipapi.co/json/")
        .then((response) => {
        let data = response.data;
        setState({
            ...state,
            countryCode: data.country_calling_code,
        });
        })
        .catch((error) => {
        console.log(error);
        });
    };

    useEffect(() => {
    getGeoInfo();
    }, []);

    // Inputs
    const [mynumber, setnumber] = useState("");
    const [otp, setotp] = useState('');
    const [show, setshow] = useState(false);
    const [final, setfinal] = useState('');
  
    // Sent OTP
    const signin = async() => {
  
        if (mynumber === "" || mynumber.length < 10){
            alert('Please enter your correct mobile no.')
            return false;
        }

        let axiosConfig = {
            method: 'HEAD',
            mode: 'no-cors',
            headers: {
              'Content-Type':  'application/json',
                "Access-Control-Allow-Origin": "*",
            }
          };
        try {
            const res =  await axios.get('http://192.168.38.28:3000/get-all-users',axiosConfig)
            const users = res.data;
            if(users.length > 0){
                const filtered = users.filter(user => {
                    return user.user_phone === mynumber;
                });
                if(filtered.length > 0){
                    if(filtered[0].active === 'active'){
                     
                        window.recaptchaVerifier = new RecaptchaVerifier('recaptcha-container', {
                            'size': 'invisible',
                        }, auth);
                
                        const appVerifier = window.recaptchaVerifier;
                
                        const no_with_country_code = `${state.countryCode}${mynumber}`;
                        
                        signInWithPhoneNumber(auth, no_with_country_code, appVerifier).then((result) => {
                            setfinal(result);
                            alert("code sent")
                            setshow(true);
                        })
                            .catch((err) => {
                                alert(err);
                                window.location.reload()
                            });
                    }
                    else{
                        toast.error('user is not active.', {
                            position: toast.POSITION.TOP_CENTER
                        });
                    }
                  } 
                  else{
    
                    const CustomToastWithLink = () => (
                        <a href="/signup">
                          User is not registered, please go to  register page.
                        </a>
                      );
    
                    toast.info(CustomToastWithLink, {
                        position: toast.POSITION.TOP_CENTER
                    });
    
                
                  }
            }
            else{
                const CustomToastWithLink = () => (
                    <a href="/signup">
                      User is not registered, please go to  register page.
                    </a>
                  );

                toast.info(CustomToastWithLink, {
                    position: toast.POSITION.TOP_CENTER
                });
            }
              
          } catch (e) {
             console.log(e);
          }
  
    }
  
    // Validate OTP
    const ValidateOtp = () => {
        if (otp === null || final === null)
            return;
        final.confirm(otp).then((result) => {
            // success
            alert("Success");
        }).catch((err) => {
            alert("Wrong code");
        })
    }
  
    return (
        <form id="otp-verify-form">
            <div style={{ display: !show ? "block" : "none" }}>
                <div className='form-group flex'>
                    <span className='country-code'>{state.countryCode ? state.countryCode :'+91'}</span>
                   <input className='form-control' name='otp-phone-no' value={mynumber} onChange={(e) => { 
                    setnumber(e.target.value) }} placeholder='Enter your Number'/>
                </div>
                <div id="recaptcha-container"></div>
                <button type="button" className="btn btn-block btn-primary mt-4" onClick={signin}>Send OTP</button>
            </div>
            <div style={{ display: show ? "block" : "none" }}>
               <div className='form-group'>
                   <input type="text" className='form-control' placeholder={"Enter your OTP"}
                    onChange={(e) => { setotp(e.target.value) }}/>
                </div>
                <Link to="#" className="btn btn-block btn-primary mt-4" onClick={ValidateOtp}>Verify</Link>
            </div>
        </form>  
    );
}
  
export default LoginVerify;