import { ResetEmailFields,New_Pass_Fields } from "../constants/formFields"
import FormAction from "./FormAction";
import Input from "./Input";
import { useForm } from 'react-hook-form';
import { ErrorMessage } from '@hookform/error-message';
import {toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { getAuth, sendPasswordResetEmail ,verifyPasswordResetCode, confirmPasswordReset } from "firebase/auth"
import axios from "axios";
import { useNavigate } from "react-router-dom";

export default function ForgotPassword(){
    const urlParams = new URLSearchParams(window.location.search)
    const mode = urlParams.get('mode');
    const actionCode = urlParams.get('oobCode')
    const navigate = useNavigate()
    const { register, handleSubmit,  formState: { errors } } = useForm();
    const onSubmit = async(data) => {
        const auth = getAuth();
        if(mode === 'resetPassword'){
            
            verifyPasswordResetCode(auth, actionCode).then((email) => {
             
                const newPassword = data.user_pass;

                const user_detail = new Object();

                user_detail.user_email = email;

                user_detail.user_pass = data.user_pass;

                // Save the new password.
                confirmPasswordReset(auth, actionCode, newPassword).then(async() => {
                  // Password reset has been confirmed and new password updated.
                  let axiosConfig = {
                    method: 'HEAD',
                    mode: 'no-cors',
                    headers: {
                      'Content-Type':  'application/json',
                        "Access-Control-Allow-Origin": "*",
                    }
                  };
                  try {
                    const res = await axios.post('http://192.168.38.28:3000/resetpassword', JSON.stringify(user_detail),axiosConfig)
                    toast.success(res.data.message, {
                       position: toast.POSITION.TOP_CENTER
                    });
                   document.getElementById("forgot_pass-form").reset();
                    setTimeout(function(){
                        navigate('/')
                    }, 1000);
                  } catch (e) {
        
                    toast.error('Something wrong with API connection !', {
                      position: toast.POSITION.TOP_CENTER
                    });
                  }
                }).catch((error) => {
                  toast.error(error.message, {
                    position: toast.POSITION.TOP_CENTER
                  });
                });
              }).catch((error) => {
                toast.error(error.message, {
                   position: toast.POSITION.TOP_CENTER
                });
              });
        }
        else{
            sendPasswordResetEmail(auth, data.email).then((resp) => {
                 // Email sent.
                toast.success('Email Sent! Please check your mail.', {
                    position: toast.POSITION.TOP_CENTER
                });
                document.getElementById("forgot_pass-form").reset();
              }).catch((error) => {
                toast.error(error.message, {
                   position: toast.POSITION.TOP_CENTER
                });
                document.getElementById("forgot_pass-form").reset();
              });  
        }
    }
        

    return(
      <form className="forgot_pass-form" id="forgot_pass-form" onSubmit={handleSubmit(onSubmit)}>
         <div className="form-fields">
            {mode === 'resetPassword' ? 
            New_Pass_Fields.map(field=>
                <div className="form-group" key={field.id}>
                    <Input
                        key={field.id}
                        labelText={field.labelText}
                        labelFor={field.labelFor}
                        id={field.id}
                        name={field.name}
                        type={field.type}
                        placeholder={field.placeholder}
                        register = { register(field.name, {required: "This is required.",pattern:field.pattern}) }
                        error = {errors}
                        autoComplete = {field.autoComplete}
                        
                    />
                    <ErrorMessage
                    errors={errors}
                    name={field.name}
                    render={({ message }) => <div className="invalid-feedback">{message}</div>}
                    />
                </div>
            )
            :
            
            ResetEmailFields.map(field=>
                    <div className="form-group" key={field.id}>
                        <Input
                            key={field.id}
                            labelText={field.labelText}
                            labelFor={field.labelFor}
                            id={field.id}
                            name={field.name}
                            type={field.type}
                            placeholder={field.placeholder}
                            register = { register(field.name, {required: "This is required.",pattern:field.pattern}) }
                            error = {errors}
                            autoComplete = {field.autoComplete}
                            
                        />
                        <ErrorMessage
                        errors={errors}
                        name={field.name}
                        render={({ message }) => <div className="invalid-feedback">{message}</div>}
                        />
                    </div>
                )
            
            }
          </div>
          <FormAction text="Reset Password" />
      </form>
    )
}