const fixedInputClass="form-control"

export default function Input({
    handleChange,
    value,
    labelText,
    labelFor,
    id,
    name,
    type,
    isRequired=false,
    placeholder,
    register,
    autoComplete
}){
  
    return(
        <>
          {labelText && <label htmlFor={labelFor} className="sr-only">{labelText}</label>}
            <input
              onChange={handleChange}
              value={value}
              id={id}
              name={name}
              type={type}
              required={isRequired}
              className={fixedInputClass}
              placeholder={placeholder}
              {...register}
              autoComplete = {autoComplete}
            />
        
          </>
    )
}