import { loginFields } from "../constants/formFields";
import FormAction from "./FormAction";
import FormExtra from "./FormExtra";
import Input from "./Input";
import { useForm } from 'react-hook-form';
import { ErrorMessage } from '@hookform/error-message';
import LoginVerify from "./otp-verfiy";
import { useNavigate } from "react-router-dom";
import React, { useState , useEffect} from 'react';
import Modal from 'react-bootstrap/Modal';
import {toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from "axios";
import { useCookies } from 'react-cookie';

import { getAuth, signInWithEmailAndPassword } from "firebase/auth";




export default function Login(){
    const [cookies, setCookie] = useCookies(['user-token']);
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false); 
    const handleShow = () => setShow(true);

    const { register, handleSubmit, formState: { errors } } = useForm();

    const [user, setUser] = useState()
    const navigate = useNavigate();
    useEffect(() => {
        const loggedInUser = localStorage.getItem("user-token");
        if (loggedInUser) {
            setUser(loggedInUser);
        }
        if(user){
            navigate('/dashboard')
        }
      }, [user , navigate]);
    const onSubmit = async(data,event) => {
        const auth = getAuth();
        signInWithEmailAndPassword(auth, data.email, data.password)
        .then((userCredential) => {
            // Signed in 
            const user = userCredential.user;
            console.log(user);
            // ...
        })
        .catch((error) => {
            // const errorCode = error.code;
            // const errorMessage = error.message;
            console.log(error.message)
        });

        

        const remember_me = event.target.remember_me.checked;

        let axiosConfig = {
            method: 'HEAD',
            mode: 'no-cors',
            headers: {
              'Content-Type':  'application/json',
                "Access-Control-Allow-Origin": "*",
            }
          };
          try {
            const res = await axios.post('http://192.168.38.28:3000/loginapi', JSON.stringify(data),axiosConfig)
            // set the state of the user
            setUser(res.data.token)
            // store the user in localStorage
            if(res.data.token){
                if(remember_me){
                    setCookie('user-token', res.data.token, { path: '/' ,maxAge:86400*15});
                }
                localStorage.setItem('user-token', res.data.token)
                toast.success(res.data.message, {
                position: toast.POSITION.TOP_CENTER
                });
            }
            else{
                toast.error(res.data.message, {
                    position: toast.POSITION.TOP_CENTER
                });
            }
            setTimeout(function(){
              document.getElementById("login-form").reset();
          }, 1000);
          } catch (e) {

            toast.error('Something wrong with API connection !', {
              position: toast.POSITION.TOP_CENTER
            });
          }
    }


    return(
        <form className="login-form" id="login-form" onSubmit={handleSubmit(onSubmit)}>
            <div className="form-fields">
            {
                loginFields.map(field=>
                    <div className="form-group" key={field.id}>
                        <Input
                            key={field.id}
                            labelText={field.labelText}
                            labelFor={field.labelFor}
                            id={field.id}
                            type={field.type}
                            placeholder={field.placeholder}
                            register = {register(field.name, {required: "This is required."})}
                        />
                         <ErrorMessage
                            errors={errors}
                            name={field.name}
                            render={({ message }) => <div className="invalid-feedback">{message}</div>}
                            />
                    </div>
                
                )
            }
            </div>
        <FormExtra/>
        <FormAction text="Login" />
        <span className="d-block text-center my-4 text-muted">— or —</span>
        <div className="social-login">
            <button type="button" onClick={handleShow} className="btn-secondary btn mb-2 d-flex justify-content-center align-items-center">
               Login with Mobile Number
            </button>
            {/* <a href="#" className="google btn d-flex justify-content-center align-items-center">
                Login with Google
            </a> */}
            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                <Modal.Title>OTP Verification</Modal.Title>
                </Modal.Header>
                <Modal.Body className="pt-5 pb-5">
                  <LoginVerify/>
                </Modal.Body>
            </Modal>
        </div>

      </form>
    )
}