import { signupFields } from "../constants/formFields"
import FormAction from "./FormAction";
import Input from "./Input";
import { useForm } from 'react-hook-form';
import { ErrorMessage } from '@hookform/error-message';
import {toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { getAuth, createUserWithEmailAndPassword,updateProfile } from "firebase/auth";


import axios from "axios";

export default function Signup(){
    const { register, handleSubmit,  formState: { errors } } = useForm();
    const onSubmit = async(data) => {

      const auth = getAuth();
      createUserWithEmailAndPassword(auth, data.email, data.psw)
        .then((userCredential) => {
          updateProfile(userCredential.user, {
            displayName: data.fname +' '+ data.lname,
          });
        })
        .catch((error) => {
          alert(error.message)
        });



      
      let axiosConfig = {
        method: 'HEAD',
        mode: 'no-cors',
        headers: {
          'Content-Type':  'application/json',
            "Access-Control-Allow-Origin": "*",
        }
      };
      try {
        const res = await axios.post('http://192.168.38.28:3000/insert-user', JSON.stringify(data),axiosConfig)
        console.log(res.data.message);
        toast.success(res.data.message, {
          position: toast.POSITION.TOP_CENTER
        });
        setTimeout(function(){
          document.getElementById("signup-form").reset();
      }, 1000);
      } catch (e) {
        // alert(e)
        toast.error('Something wrong with API connection !', {
          position: toast.POSITION.TOP_CENTER
        });
        // setTimeout(window.location.reload(), 2000);
      }
    
        
    }

    return(
      <form className="signup-form" id="signup-form" onSubmit={handleSubmit(onSubmit)}>
         
         <div className="form-fields">
            {
                signupFields.map(field=>
                    <div className="form-group" key={field.id}>
                        <Input
                            key={field.id}
                            labelText={field.labelText}
                            labelFor={field.labelFor}
                            id={field.id}
                            name={field.name}
                            type={field.type}
                            placeholder={field.placeholder}
                            register = { register(field.name, {required: "This is required.",pattern:field.pattern}) }
                            error = {errors}
                            
                        />
                        <ErrorMessage
                        errors={errors}
                        name={field.name}
                        render={({ message }) => <div className="invalid-feedback">{message}</div>}
                        />
                    </div>
                )
            }
          </div>
          <FormAction text="Register Account" />
      </form>
    )
}